.uniaxial-catalog {
  margin-bottom: 60px;

  @include vp-1199 {
    margin-bottom: 50px;
  }

  @include vp-767 {
    margin-bottom: 40px;
  }
}

.uniaxial-catalog__tabs-controls {
  @include vp-767 {
    flex-direction: column;
    row-gap: 12px;
    margin-bottom: 20px;
  }
}

.uniaxial-catalog__tabs-control {
  @include vp-1199 {
    padding: 10px 16px;
    font-size: 14px;
    line-height: 14px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 12px;
  }
}
