.nav {
  &--header {
    @include vp-1199 {
      background-color: $color-default-white;
      position: fixed;
      z-index: 20;
      margin: 0;
      top: 0;
      left: 0;
      right: 0;
      opacity: 0;
      visibility: hidden;
      transition: background-color 0.3s ease;
      pointer-events: none;
      max-height: 100vh;
      overflow-y: auto;
    }

    .is-open & {
      top: 0;
      padding-top: 49px;
      padding-bottom: 70px;
      padding-left: 39px;
      opacity: 1;
      pointer-events: all;
      visibility: visible;

      @include vp-767 {
        padding-top: 22px;
        padding-left: 9px;
        padding-bottom: 0;
      }

      .nav__item {
        opacity: 1;
        transform: translateX(0);
      }
    }
  }

  // &--footer {
  //   @include vp-1199 {
  //     padding-top: 4px;
  //     margin-left: auto;
  //   }

  //   @include vp-767 {
  //     width: 100%;
  //   }
  // }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;

  & a {
    padding: 3px 5px;
    text-decoration: none;
    font-size: 16px;
    font-weight: 400;
    line-height: 16px;
    border: 1px solid $color-transparent;
    user-select: none;
    transition: color $trans-default, border-color $trans-default;

    @include vp-1199 {
      font-size: 14px;
      line-height: 14px;
    }

    &--disabled {
      color: $color-default-black;
      opacity: 0.5;
      pointer-events: none;
    }
  }
}

.nav__list--header {
  column-gap: 21px;
  row-gap: 22px;

  @include vp-1199 {
    flex-direction: column;
    max-width: unset;
  }

  @include vp-767 {
    align-items: flex-start;
    row-gap: 16px;
    margin-bottom: 48px;
  }

  & li {
    @include vp-1199 {
      opacity: 0;
      transition: opacity ease-out 0.15s, transform ease-out 0.15s;
      transform: translateX(-15px);
    }
  }

  & a[href] {
    color: $text-dark;

    @include vp-1200 {
      @include hover {
        color: $text-action;
        outline: none;
        border-bottom: 1px solid $color-default-green;

        @include active {
          border-color: $color-transparent;
        }
      }

      @include focus {
        color: $text-action;
        outline: none;
        border: 1px solid $color-default-green;
      }
    }

    @include vp-1199 {
      color: $text-dark;
      font-size: 16px;
      font-weight: 700;
      line-height: 16px;
      letter-spacing: 0.64px;
      text-transform: uppercase;

      @include hover {
        color: $text-action;
        outline: none;
        border-bottom: 1px solid $text-action;

        @include active {
          color: $text-dark;
        }
      }

      @include focus {
        color: $text-dark;
        border: 1px solid $text-action;
      }
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 14px;
      letter-spacing: 0.56px;
    }
  }
}

.nav__list--footer {
  column-gap: 21px;
  row-gap: 22px;

  @include vp-767 {
    row-gap: 12px;
  }

  & a {
    color: $text-dark;

    @include vp-1200 {
      @include hover {
        color: $text-action;
        outline: none;
        border-bottom: 1px solid $color-default-green;

        @include active {
          border-color: $color-transparent;
        }
      }

      @include focus {
        color: $text-action;
        outline: none;
        border: 1px solid $color-default-green;
      }
    }

    @include vp-1199 {
      letter-spacing: 0.64px;

      @include hover {
        color: $text-action;
        outline: none;
        border-bottom: 1px solid $text-action;

        @include active {
          color: $text-dark;
        }
      }

      @include focus {
        color: $text-dark;
        border: 1px solid $text-action;
      }
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 14px;
      letter-spacing: 0.56px;
    }
  }
}

