  border-bottom: 1px solid #01aa52;
  outline: none;
}
.page-footer__link--phone.focus-visible:focus:active {
  color: #01aa52;
  border-color: rgba(255, 255, 255, 0);
}

.catalog__title {
  margin-bottom: 30px;
}
@media (max-width: 1199px) {
  .catalog__title {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .catalog__title {
    margin-bottom: 15px;
  }
}

.catalog__tabs-controls {
  margin-bottom: 24px;
  display: flex;
  justify-content: center;
  column-gap: 24px;
}
@media (max-width: 1199px) {
  .catalog__tabs-controls {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .catalog__tabs-controls {
    margin-bottom: 16px;
  }
}

.catalog__tabs-control {
  align-self: center;
  padding: 12px 20px;
  border-radius: 16px;
  border: 1px solid #5b5b5b;
  background-color: #01aa52;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  color: #f6f5f5;
  text-transform: uppercase;
  transition: border-color 0.5s ease, background-color 0.5s ease, color 0.5s ease;
  cursor: pointer;
}
@media (max-width: 767px) {
  .catalog__tabs-control {
    font-size: 16px;
    line-height: 16px;
  }
}
@media (hover: hover) {
  .catalog__tabs-control:hover:not(.focus-visible) {
    outline: none;
    color: #01aa52;
    background-color: #ffffff;
    border: 1px solid #01aa52;
  }
  .catalog__tabs-control:hover:not(.focus-visible):active {
    opacity: 0.7;
  }
}
.catalog__tabs-control.focus-visible:focus {
  outline: none;
  color: #01aa52;
  background-color: #ffffff;
  border: 1px solid #01aa52;
}
.catalog__tabs-control.focus-visible:focus:active {
  opacity: 0.7;
}
.catalog__tabs-control--disabled {
  color: #5b5b5b;
  pointer-events: none;
  background: rgba(2, 102, 193, 0.1);
}
.catalog__tabs-control.is-active {
  color: #01aa52;
  background-color: #ffffff;
  border: 1px solid #01aa52;
}

.catalog__tabs-element,
.catalog__element {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  grid-row-gap: 30px;
}
@media (max-width: 1199px) {
  .catalog__tabs-element,
.catalog__element {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
  }
}
@media (max-width: 767px) {
  .catalog__tabs-element,
.catalog__element {
    grid-template-columns: 1fr;
    grid-column-gap: 0;
    grid-row-gap: 12px;
  }
}

.catalog__tabs-slide {
  cursor: pointer;
}

